import React, { useState, useEffect } from 'react';
import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  Modal,
  TextInput,
  ScrollView,
  Alert,
  Platform,
  useWindowDimensions,
} from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import { useNavigation } from '@react-navigation/native';
import { useQuivers } from '../hooks/useQuivers';
import { BaseProduct } from '../types/scraper';
import { Quiver } from '../types/quiver';
import { useTheme } from '../theme/ThemeContext';
import { CONTAINER_PADDING, MOBILE_BREAKPOINT } from '../constants/layout';
import { spacing } from '../theme/spacing';
import { RootStackNavigationProp } from '../types/navigation';
import { trackAction, ANALYTICS_EVENTS } from '../utils/analytics';
import { logger } from '../utils/logger';
import { useApp } from '../context/AppContext';

interface QuiverManagerProps {
  selectedFoils?: BaseProduct[];
  onClose?: () => void;
  initialTab?: 'save' | 'saved';
  standalone?: boolean;
  isModalVisible?: boolean;
  hideStandaloneButton?: boolean;
  onQuiverSaved?: (quiverId: string, quiverName: string) => void;
}

export const QuiverManager: React.FC<QuiverManagerProps> = ({
  selectedFoils = [],
  onClose,
  initialTab = 'save',
  standalone = false,
  isModalVisible,
  hideStandaloneButton = false,
  onQuiverSaved,
}) => {
  const { colors } = useTheme();
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;
  const navigation = useNavigation<RootStackNavigationProp>();
  const { setSelectedFoils } = useApp();

  const [isModalVisibleInternal, setIsModalVisibleInternal] = useState(!standalone);
  const modalVisible = isModalVisible !== undefined ? isModalVisible : isModalVisibleInternal;

  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [isPublic, setIsPublic] = useState(false);
  const [activeTab, setActiveTab] = useState<'save' | 'saved'>(initialTab);
  const [editingQuiverId, setEditingQuiverId] = useState<string | null>(null);
  const [editingName, setEditingName] = useState('');

  const {
    quivers,
    isLoading,
    error,
    createQuiver,
    deleteQuiver,
    reloadQuivers,
    updateQuiver
  } = useQuivers();

  const handleCreateQuiver = async () => {
    if (!name.trim()) {
      if (Platform.OS === 'web') {
        window.alert('Please enter a name for your quiver');
      } else {
        Alert.alert('Error', 'Please enter a name for your quiver');
      }
      return;
    }

    try {
      const result = await createQuiver({
        name: name.trim(),
        description: description.trim(),
        foils: selectedFoils,
        is_public: isPublic,
      });

      if (result) {
        trackAction(ANALYTICS_EVENTS.SAVE_QUIVER, {
          foilCount: selectedFoils.length,
          isPublic,
        });
        
        if (Platform.OS === 'web') {
          window.alert('Quiver saved successfully');
        } else {
          Alert.alert('Success', 'Quiver saved successfully');
        }
        setName('');
        setDescription('');
        setIsPublic(false);
        setIsModalVisibleInternal(false);
        onClose?.();
        onQuiverSaved?.(result.id, result.name);
      } else {
        throw new Error('Failed to create quiver - no result returned');
      }
    } catch (error) {
      console.error('Error creating quiver:', error);
      if (Platform.OS === 'web') {
        window.alert(`Failed to save quiver: ${error instanceof Error ? error.message : 'Unknown error'}`);
      } else {
        Alert.alert('Error', `Failed to save quiver: ${error instanceof Error ? error.message : 'Unknown error'}`);
      }
    }
  };

  const handleDeleteQuiver = async (quiverId: string) => {
    try {
      const userConfirmed = await new Promise<boolean>((resolve) => {
        Alert.alert(
          'Delete Quiver',
          'Are you sure you want to delete this quiver? This action cannot be undone.',
          [
            {
              text: 'Cancel',
              style: 'cancel',
              onPress: () => resolve(false),
            },
            {
              text: 'Delete',
              style: 'destructive',
              onPress: () => resolve(true),
            },
          ],
          { cancelable: false }
        );
      });

      if (!userConfirmed) return;

      const success = await deleteQuiver(quiverId);
      
      if (success) {
        trackAction(ANALYTICS_EVENTS.DELETE_QUIVER, {
          quiverId,
        });
        Alert.alert('Success', 'Quiver deleted successfully');
      } else {
        Alert.alert('Error', 'Failed to delete quiver. Please try again.');
      }
    } catch (error) {
      console.error('Error deleting quiver:', error);
      Alert.alert('Error', 'An unexpected error occurred. Please try again.');
    }
  };

  const handleViewQuiver = (quiver: Quiver) => {
    // Close the modal first
    setIsModalVisibleInternal(false);
    onClose?.();

    // Clear any existing selected foils before navigating
    setSelectedFoils([]);

    // Navigate to Quiver screen with the quiver's data
    navigation.navigate('Quiver', {
        quiverId: quiver.id,
        quiverName: quiver.name,
        foil_ids: quiver.foil_ids || []
    });
  };

  const handleStartRename = (quiver: Quiver) => {
    setEditingQuiverId(quiver.id);
    setEditingName(quiver.name);
  };

  const handleSaveRename = async () => {
    if (!editingQuiverId || !editingName.trim()) return;

    try {
      const success = await updateQuiver(editingQuiverId, { name: editingName.trim() });
      if (success) {
        trackAction(ANALYTICS_EVENTS.UPDATE_QUIVER, {
          quiverId: editingQuiverId,
          action: 'rename',
        });
        
        if (Platform.OS === 'web') {
          window.alert('Quiver renamed successfully');
        } else {
          Alert.alert('Success', 'Quiver renamed successfully');
        }
        setEditingQuiverId(null);
        setEditingName('');
        reloadQuivers();
      }
    } catch (error) {
      console.error('Error renaming quiver:', error);
      Alert.alert('Error', 'Failed to rename quiver');
    }
  };

  const getFoilCount = (quiver: Quiver) => {
    if (quiver.foil_ids && Array.isArray(quiver.foil_ids)) {
        return quiver.foil_ids.length;
    }
    return 0;
  };

  const renderQuiverList = () => {
    if (isLoading) {
      return (
        <View style={styles.centerContent}>
          <Text style={[styles.message, { color: colors.text.secondary }]}>
            Loading quivers...
          </Text>
        </View>
      );
    }

    if (error) {
      return (
        <View style={styles.centerContent}>
          <Text style={[styles.message, { color: colors.error }]}>
            {error}
          </Text>
        </View>
      );
    }

    if (quivers.length === 0) {
      return (
        <View style={styles.centerContent}>
          <Text style={[styles.message, { color: colors.text.secondary }]}>
            No saved quivers yet
          </Text>
        </View>
      );
    }

    return quivers.map((quiver) => {
      return (
        <View
          key={quiver.id}
          style={[
            styles.quiverItem,
            { borderBottomColor: colors.border }
          ]}
        >
          <TouchableOpacity 
            style={styles.quiverInfo}
            onPress={() => handleViewQuiver(quiver)}
          >
            {editingQuiverId === quiver.id ? (
              <View style={styles.renameContainer}>
                <TextInput
                  style={[
                    styles.renameInput,
                    { 
                      color: colors.text.primary,
                      borderColor: colors.border,
                      backgroundColor: colors.background.secondary
                    }
                  ]}
                  value={editingName}
                  onChangeText={setEditingName}
                  placeholder="Enter new name"
                  placeholderTextColor={colors.text.tertiary}
                  autoFocus
                />
                <TouchableOpacity
                  style={[styles.renameButton, { backgroundColor: colors.error }]}
                  onPress={() => {
                    setEditingQuiverId(null);
                    setEditingName('');
                  }}
                >
                  <Text style={[styles.renameButtonText, { color: colors.text.inverse }]}>Cancel</Text>
                </TouchableOpacity>
                <TouchableOpacity
                  style={[styles.renameButton, { backgroundColor: colors.primary }]}
                  onPress={handleSaveRename}
                >
                  <Text style={[styles.renameButtonText, { color: colors.text.inverse }]}>Save</Text>
                </TouchableOpacity>
              </View>
            ) : (
              <>
                <View style={styles.quiverNameContainer}>
                  <Text style={[styles.quiverName, { color: colors.text.primary }]}>
                    {quiver.name}
                  </Text>
                  <TouchableOpacity
                    onPress={() => handleStartRename(quiver)}
                    style={styles.renameIcon}
                  >
                    {Platform.OS === 'web' ? (
                      <Text style={{ color: colors.text.secondary }}></Text>
                    ) : (
                      <Ionicons name="pencil" size={16} color={colors.text.secondary} />
                    )}
                  </TouchableOpacity>
                </View>
                {quiver.description && (
                  <Text style={[styles.quiverDescription, { color: colors.text.secondary }]}>
                    {quiver.description}
                  </Text>
                )}
                <Text style={[styles.quiverMeta, { color: colors.text.tertiary }]}>
                  {getFoilCount(quiver)} foils  {new Date(quiver.updated_at).toLocaleDateString()}
                  {quiver.is_public && '  Public'}
                </Text>
              </>
            )}
          </TouchableOpacity>
          {Platform.OS === 'web' ? (
            <button
              type="button"
              onClick={() => {
                try {
                  const confirmed = window.confirm('Are you sure you want to delete this quiver? This action cannot be undone.');
                  if (confirmed) {
                    deleteQuiver(quiver.id).then(success => {
                      if (success) {
                        reloadQuivers();
                      } else {
                        window.alert('Failed to delete quiver');
                      }
                    }).catch(error => {
                      console.error('Delete error:', error);
                      window.alert('Error deleting quiver');
                    });
                  }
                } catch (error) {
                  console.error('Delete error:', error);
                  window.alert('Error in delete operation');
                }
              }}
              style={{
                padding: '8px',
                borderWidth: '1px',
                borderStyle: 'solid',
                borderColor: colors.error,
                borderRadius: '8px',
                minWidth: '70px',
                backgroundColor: colors.background.primary,
                cursor: 'pointer',
                color: colors.error,
                fontWeight: '600',
                fontSize: '14px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                zIndex: 10,
                position: 'relative',
                pointerEvents: 'auto',
              }}
            >
              Delete
            </button>
          ) : (
            <TouchableOpacity
              style={[
                styles.deleteButton, 
                { 
                  borderColor: colors.error,
                  backgroundColor: colors.background.primary 
                }
              ]}
              onPress={() => {
                handleDeleteQuiver(quiver.id);
              }}
            >
              <Ionicons name="trash-outline" size={20} color={colors.error} />
            </TouchableOpacity>
          )}
        </View>
      );
    });
  };

  const content = (
    <>
      <View style={[styles.modalHeader, { borderBottomColor: colors.border }]}>
        <Text style={[styles.modalTitle, { color: colors.text.primary }]}>
          Manage Quivers
        </Text>
        {onClose && (
          <TouchableOpacity onPress={onClose}>
            {Platform.OS === 'web' ? (
              <Text style={{ fontSize: 24, color: colors.text.primary }}></Text>
            ) : (
              <Ionicons name="close" size={24} color={colors.text.primary} />
            )}
          </TouchableOpacity>
        )}
      </View>

      {selectedFoils.length > 0 && (
        <View style={[styles.tabContainer, { borderBottomColor: colors.border }]}>
          <TouchableOpacity
            style={[
              styles.tab,
              activeTab === 'save' && [
                styles.activeTab,
                { borderBottomColor: colors.primary }
              ]
            ]}
            onPress={() => setActiveTab('save')}
          >
            <Text
              style={[
                styles.tabText,
                { color: activeTab === 'save' ? colors.primary : colors.text.secondary }
              ]}
            >
              Save Quiver
            </Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={[
              styles.tab,
              activeTab === 'saved' && [
                styles.activeTab,
                { borderBottomColor: colors.primary }
              ]
            ]}
            onPress={() => setActiveTab('saved')}
          >
            <Text
              style={[
                styles.tabText,
                { color: activeTab === 'saved' ? colors.primary : colors.text.secondary }
              ]}
            >
              Saved Quivers
            </Text>
          </TouchableOpacity>
        </View>
      )}

      <ScrollView style={styles.modalBody}>
        {activeTab === 'save' && selectedFoils.length > 0 ? (
          <View style={styles.form}>
            <View style={styles.formField}>
              <Text style={[styles.label, { color: colors.text.secondary }]}>
                Name
              </Text>
              <TextInput
                style={[
                  styles.input,
                  { 
                    color: colors.text.primary,
                    borderColor: colors.border,
                    backgroundColor: colors.background.secondary
                  }
                ]}
                value={name}
                onChangeText={setName}
                placeholder="Enter quiver name"
                placeholderTextColor={colors.text.tertiary}
              />
            </View>

            <View style={styles.formField}>
              <Text style={[styles.label, { color: colors.text.secondary }]}>
                Description (optional)
              </Text>
              <TextInput
                style={[
                  styles.input,
                  styles.textArea,
                  { 
                    color: colors.text.primary,
                    borderColor: colors.border,
                    backgroundColor: colors.background.secondary
                  }
                ]}
                value={description}
                onChangeText={setDescription}
                placeholder="Enter description"
                placeholderTextColor={colors.text.tertiary}
                multiline
                numberOfLines={3}
              />
            </View>

            <TouchableOpacity
              style={styles.publicToggle}
              onPress={() => setIsPublic(!isPublic)}
            >
              {Platform.OS === 'web' ? (
                <Text style={{ 
                  fontSize: 24, 
                  color: isPublic ? colors.primary : colors.text.secondary,
                  marginRight: 8
                }}>
                  {isPublic ? '' : ''}
                </Text>
              ) : (
                <Ionicons
                  name={isPublic ? 'checkbox' : 'square-outline'}
                  size={24}
                  color={isPublic ? colors.primary : colors.text.secondary}
                />
              )}
              <Text style={[styles.publicToggleText, { color: colors.text.primary }]}>
                Make this quiver public
              </Text>
            </TouchableOpacity>

            <TouchableOpacity
              style={[
                styles.createButton,
                { backgroundColor: colors.primary }
              ]}
              onPress={handleCreateQuiver}
            >
              <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
                Save Quiver
              </Text>
            </TouchableOpacity>
          </View>
        ) : (
          <View style={styles.quiverList}>
            {renderQuiverList()}
          </View>
        )}
      </ScrollView>
    </>
  );

  if (standalone) {
    return content;
  }

  return (
    <>
      {selectedFoils.length > 0 && !standalone && !isModalVisible && !hideStandaloneButton && (
        <TouchableOpacity
          style={[
            styles.saveButton,
            { backgroundColor: colors.primary }
          ]}
          onPress={() => setIsModalVisibleInternal(true)}
        >
          {Platform.OS === 'web' ? (
            <Text style={{ fontSize: 20, color: colors.text.inverse, marginRight: 8 }}>+</Text>
          ) : (
            <Ionicons name="bookmark-outline" size={20} color={colors.text.inverse} />
          )}
          <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
            Save Quiver
          </Text>
        </TouchableOpacity>
      )}
      <Modal
        visible={modalVisible}
        transparent
        animationType="fade"
        onRequestClose={() => {
          setIsModalVisibleInternal(false);
          onClose?.();
        }}
      >
        <TouchableOpacity
          style={[
            styles.modalOverlay,
            standalone && styles.standaloneOverlay
          ]}
          activeOpacity={1}
          onPress={() => {
            setIsModalVisibleInternal(false);
            onClose?.();
          }}
        >
          <TouchableOpacity
            style={[
              styles.modalContent,
              { backgroundColor: colors.background.primary },
              isMobile && styles.modalContentMobile
            ]}
            activeOpacity={1}
            onPress={(e) => {
              e.preventDefault();
              e.stopPropagation();
            }}
          >
            {content}
          </TouchableOpacity>
        </TouchableOpacity>
      </Modal>
    </>
  );
};

